import { derived } from 'svelte/store';
import { listStore } from './List';
export const listColumns = derived(listStore, ($listStore) => {
    return $listStore.tables.reduce((accum, value) => {
        const columns = value.columns.map((column) => (Object.assign(Object.assign({}, column), { id: `${value.id}/${column.id}`, name: `${column.name} (${value.id})` })));
        return [...accum, ...columns];
    }, []);
});
